/*
 *  FAQ Accordions
 *    Styles for the faq accordions section
 *
 *  Version:
 *    1.0.0 - 2021/09/11
 */

.c-faq-accordions {
  background-color: $s-color--background-subdued;

  &__inner {
    padding: 4.375rem $s-gutter--edge-mobile 5.375rem;
  }

  &__title {
    margin-bottom: 0.625rem;
  }

  .o-accordion__label {
    width: 77%;
    display: block;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      padding: 4.375rem 2.75rem 5.375rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 8.625rem 10% 10.25rem;
    }

    &__faqs {
      max-width: 72.25rem;
      margin: 0 auto;
    }

    &__title {
      max-width: 72.25rem;
      margin: 0 auto 1.6875rem;
    }

    .o-accordion {
      &__label {
        text-align: left;
        position: relative;
        left: 0;
        top: 0;
        transform: initial;
        width: initial;
      }

      &__minus,
      &__plus{
        display: block;
        width: 1.4375rem;
        height: 1.4375rem;
      }

      &__heading{
        padding: 2.5rem 0;
      }
    }
  }
}
