.o-accordion {
  border-bottom: 1px solid $s-color--border-20;
  overflow: hidden;

  &[open] > &__heading {
    .o-accordion__plus {
      transform: translateY(-50%) rotate(0deg);
      opacity: 0;
    }
    .o-accordion__minus {
      opacity: 1;
    }
  }

  &__tab-blob {
    display: none;
  }

  &__heading {
    position: relative;
    padding: 2rem 0;
    cursor: pointer;

    &::marker {
      content: "";
    }

    &::-webkit-details-marker {
      // hide on Safari
      display: none;
    }
  }

  &__plus {
    width: 1rem;
    height: 1rem;
    position: absolute;
    right: 0;

    opacity: 1;
    @include t-absolute-center-y();
    transform: translateY(-50%) rotate(90deg);
    transition: all .2s;
  }
  &__minus {
    width: 1rem;
    height: 1rem;
    position: absolute;
    right: 0;
    top: 0;
    opacity: 0;
    @include t-absolute-center-y();
    transition: all .2s;
  }

  &__content {
    padding: 0 0 3rem 0;
    font-size: .9375rem;
    font-weight: 400;
  }

  @include t-media-query($s-medium-up) {
    &.is-active .o-accordion__tab-blob{
      fill: $s-color--btn-background;
    }

    &[open] {
      .o-accordion__tab-blob {
        opacity: 1;
      }
    }

    &__plus {
      display: none;
    }
    &__minus {
      display: none;
    }

    &__tab-blob {
      display: inline-block;
    }

    &__label {
      @include t-absolute-center-x-y();
      width: 100%;
      text-align: center;
      top: 45%;
    }
  }
}
